import React, { useState } from 'react';
import {AppBar, Tabs, Tab, Typography, Box, styled} from '@mui/material';
import FinalExamScoreCalculator from './pages/FinalExamScoreCalculator';
import TrainingScoreCalculator from './pages/TrainingScoreCalculator';
import SPAnalysis from './pages/SPAnalysis';
import BorrowSupportCards from "./pages/BorrowSupportCards";
import CanvasNestComponent from "./components/CanvasNestComponent";
import SupportCardList from "./pages/SupportCardList";

const CustomTabs = styled(Tabs)`
  background-color: #3f51b5;
  .MuiTabs-indicator {
    background-color: #ffeb3b;
  }
`;

const CustomTab = styled(Tab)`
  color: #ffffff;
  &.Mui-selected {
    color: #ffeb3b;
  }
`;

const TabPanel = (props) => {
    const { children, value, index, ...other } = props;
    return (
        <div
            role="tabpanel"
            hidden={value !== index}
            id={`tabpanel-${index}`}
            aria-labelledby={`tab-${index}`}
            {...other}
        >
            {value === index && (
                <Box sx={{ p: 3 }}>
                    <Typography>{children}</Typography>
                </Box>
            )}
        </div>
    );
};

const App = () => {
    const [value, setValue] = useState(0);

    const handleChange = (event, newValue) => {
        setValue(newValue);
    };

    return (
        <>
            <CanvasNestComponent />
            <AppBar position="static">
                <CustomTabs value={value} onChange={handleChange} centered>
                    <CustomTab label="期末得分" />
                    <CustomTab label="追加训练" />
                    <CustomTab label="SP分析" />
                    <CustomTab label="借支援卡" />
                    <CustomTab label="支援卡一览" />
                </CustomTabs>
            </AppBar>
            <TabPanel value={value} index={0}>
                <FinalExamScoreCalculator />
            </TabPanel>
            <TabPanel value={value} index={1}>
                <TrainingScoreCalculator />
            </TabPanel>
            <TabPanel value={value} index={2}>
                <SPAnalysis />
            </TabPanel>
            <TabPanel value={value} index={3}>
                <BorrowSupportCards />
            </TabPanel>
            <TabPanel value={value} index={4}>
                <SupportCardList />
            </TabPanel>
        </>
    );
};

export default App;
