import React, { useState } from 'react';
import {
    Table, TableBody, TableCell, TableContainer, TableHead, TableRow, Paper, TextField, InputAdornment, IconButton
} from '@mui/material';
import SearchIcon from '@mui/icons-material/Search';
import categories from '../data/categories.json';

const getUniqueValues = (data, key) => {
    return [...new Set(data.map(item => item[key]))];
};

const FilterSelect = ({ name, value, options, onChange }) => (
    <div style={{ display: 'flex', alignItems: 'center', justifyContent: 'center' }}>
        <select name={name} value={value} onChange={onChange}>
            <option value="">全部</option>
            {options.map((option) => (
                <option key={option} value={option}>{option}</option>
            ))}
        </select>
    </div>
);

const SupportCardList = () => {
    const [filters, setFilters] = useState({
        rarity: '',
        attribute: '',
        plan: '',
        effectType: '',
        hand: '',
        attributeUp: ''
    });
    const [searchQuery, setSearchQuery] = useState('');

    const handleFilterChange = (event) => {
        const { name, value } = event.target;
        setFilters({
            ...filters,
            [name]: value
        });
    };

    const handleSearchChange = (event) => {
        setSearchQuery(event.target.value);
    };

    const filteredCategories = categories.filter((category) => {
        return (
            (filters.rarity === '' || category.rarity === filters.rarity) &&
            (filters.attribute === '' || category.attribute === filters.attribute) &&
            (filters.plan === '' || category.plan === filters.plan) &&
            (filters.effectType === '' || category.effect_type === filters.effectType) &&
            (filters.hand === '' || category.hand === filters.hand) &&
            (filters.attributeUp === '' || category.attribute_up === filters.attributeUp) &&
            (category.data_name.toLowerCase().includes(searchQuery.toLowerCase()))
        );
    });

    const filterOptions = {
        rarity: getUniqueValues(categories, 'rarity'),
        attribute: getUniqueValues(categories, 'attribute'),
        plan: getUniqueValues(categories, 'plan'),
        effectType: getUniqueValues(categories, 'effect_type'),
        hand: getUniqueValues(categories, 'hand'),
        attributeUp: getUniqueValues(categories, 'attribute_up')
    };

    return (
        <div>
            <TextField
                label="搜索"
                variant="outlined"
                value={searchQuery}
                onChange={handleSearchChange}
                style={{ marginBottom: '20px', width: '100%' }}
                InputProps={{
                    endAdornment: (
                        <InputAdornment position="end">
                            <IconButton>
                                <SearchIcon />
                            </IconButton>
                        </InputAdornment>
                    )
                }}
            />
            <TableContainer component={Paper}>
                <Table>
                    <TableHead>
                        <TableRow>
                            <TableCell align="center">图片</TableCell>
                            <TableCell align="center">卡名</TableCell>
                            <TableCell align="center">
                                <span>稀有度</span>
                                <FilterSelect
                                    name="rarity"
                                    value={filters.rarity}
                                    options={filterOptions.rarity}
                                    onChange={handleFilterChange}
                                />
                            </TableCell>
                            <TableCell align="center">
                                <span>属性</span>
                                <FilterSelect
                                    name="attribute"
                                    value={filters.attribute}
                                    options={filterOptions.attribute}
                                    onChange={handleFilterChange}
                                />
                            </TableCell>
                            <TableCell align="center">
                                <span>计划</span>
                                <FilterSelect
                                    name="plan"
                                    value={filters.plan}
                                    options={filterOptions.plan}
                                    onChange={handleFilterChange}
                                />
                            </TableCell>
                            <TableCell align="center">
                                <span>效果类型</span>
                                <FilterSelect
                                    name="effectType"
                                    value={filters.effectType}
                                    options={filterOptions.effectType}
                                    onChange={handleFilterChange}
                                />
                            </TableCell>
                            <TableCell align="center">效果</TableCell>
                            <TableCell align="center">
                                <span>手牌</span>
                                <FilterSelect
                                    name="hand"
                                    value={filters.hand}
                                    options={filterOptions.hand}
                                    onChange={handleFilterChange}
                                />
                            </TableCell>
                            <TableCell align="center">
                                <span>属性提升</span>
                                <FilterSelect
                                    name="attributeUp"
                                    value={filters.attributeUp}
                                    options={filterOptions.attributeUp}
                                    onChange={handleFilterChange}
                                />
                            </TableCell>
                            <TableCell align="center">特殊效果</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {filteredCategories.map((category) => (
                            <TableRow key={category.data_id}>
                                <TableCell>
                                    <img src={category.src} alt={category.data_name} style={{ height: '50px' }} />
                                </TableCell>
                                <TableCell>{category.data_name}</TableCell>
                                <TableCell>{category.rarity}</TableCell>
                                <TableCell>{category.attribute}</TableCell>
                                <TableCell>{category.plan}</TableCell>
                                <TableCell>{category.effect_type}</TableCell>
                                <TableCell style={{ whiteSpace: 'pre-line' }}>{category.effect}</TableCell>
                                <TableCell>{category.hand}</TableCell>
                                <TableCell>{category.attribute_up}</TableCell>
                                <TableCell>
                                    {category.special_effects.map((special_effect, index) => (
                                        <div key={index}>{special_effect}</div>
                                    ))}
                                </TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </div>
    );
};

export default SupportCardList;
